(function() {
  'use strict';

  angular
    .module('dc')
    .controller('MainController', MainController);

  /** @ngInject */
    function MainController($timeout, webDevTec, toastr, $mdDialog, $scope, $mdMedia, characterService, $document) {
        var vm = this;
        vm.characters = [];





        vm.showAdvanced = function(ev) {
            $mdDialog.show({
                controller: DialogController,
                templateUrl: '/app/components/newCharacter/new-character.dialog.html',
                parent: angular.element($document[0].body),//
                // parent: angular.element(document.body)
                targetEvent: ev,
                clickOutsideToClose:true,
                controllerAs: 'dialog'
                // .openFrom(ev)
            })
            .then(function(character) {
                vm.characters.push(character);
            }, function() {
                //Dialog Canceled
            });
        };

        vm.getCharacters = function(){
            //$scope.characters = characterService.getCharacters();
            characterService.getCharacters().then(function(characters){
                vm.characters = characters;
            });
        };

        function DialogController($scope, $mdDialog, dbService) {
            var vm = this;

            vm.hide = function() {
                $mdDialog.hide();
            };

            vm.cancel = function() {
                $mdDialog.cancel();
            };

            vm.answer = function(answer) {
                $mdDialog.hide(answer);
            };

            //use dbService to save a new character to the database
            vm.newCharacter = function(character){
                var db = dbService.newDB();
                db.characters.add(character);
                $mdDialog.hide(character);
            };
        }

        activate();

        function activate() {
          //Get the list of characters from the db
          vm.getCharacters();
        }
    }
})();

(function() {
  'use strict';

  angular
    .module('dc')
    .controller('SettingsController', SettingsController);

  /** @ngInject */
    function SettingsController(toastr, $mdDialog, $document, dbService) {
        var vm = this;
        vm.deleteDB = deleteDB;
        vm.showAlert = showAlert;
        vm.showConfirm = showConfirm;
        vm.populateDB = populateDB;

        function deleteDB() {
          var db = dbService.newDB();
          dbService.deleteDB(db);
        }

        function showAlert(ev) {
          $mdDialog.show(
            $mdDialog.alert()
              .parent(angular.element($document[0].querySelector('#popupContainer')))
              .clickOutsideToClose(true)
              .title('Delete Database')
              .textContent('Are you sure you want to delete the Database?')
              .ariaLabel('Delete DB Dialog')
              .ok('Confirm')
              .targetEvent(ev)
          );
        }

        function showConfirm(ev) {
          var confirm = $mdDialog.confirm()
                .title('Confirm Reset Database')
                .textContent('Are you sure you want to reset the database? Any saved data will be lost.')
                .ariaLabel('Database Delete confirmation Dialog')
                .targetEvent(ev)
                .ok('Reset')
                .cancel('Cancel');
          $mdDialog.show(confirm).then(function() {
            //Delete the database
            deleteDB();
          }, function() {
            //Dialog Cancelled
          });
        }

        function populateDB() {
          //Populates the db
          var db = dbService.newDB();
          deleteDB();
          dbService.populateDB(db);
        }

    }
})();

