(function() {
    'use strict';

    angular
        .module('dc')
        .controller('basicInfoController', basicInfoController);

    // basicInfoController.$inject = ['dependencies'];

    /* @ngInject */
    function basicInfoController($http, $scope, $rootScope, basicInfoService) {
        var vm = this;

        //Get Races
        vm.getRaces = function() {
          basicInfoService.getRaces().then(function(races){
            vm.races = races;
            $scope.$digest();
          });
        }

        //Get backgrounds
        vm.getBackgrounds = function() {
          basicInfoService.getBackgrounds().then(function(backgrounds){
            vm.backgrounds = backgrounds;
            $scope.$digest();
          });
        }

        //Get the list of classes
        vm.getClasses = function() {
          basicInfoService.getClasses().then(function(classes){
            vm.classes = classes;
            $scope.$digest();
          });
        }

        //Get the list of subraces based on race name
        vm.getSubraces = function(raceName) {
          vm.subrace = undefined;
          basicInfoService.getSubraces(raceName).then(function(subraces){
            vm.subraces = subraces;
            $scope.$digest();
          });
        }

        //Get the list of alignments
        vm.getAlignments = function() {
          basicInfoService.getAlignments().then(function(alignments){
            vm.alignments = alignments;
            $scope.$digest();
          });
        }

        vm.getArchetypes = function (className) {
          basicInfoService.getArchetypes(className).then(function(archetypes){
            vm.archetypes = archetypes;
            $scope.$digest();
          });
          $rootScope.$emit('classChanged', className);
        }

        activate();

        function activate() {
          vm.getRaces();
          vm.getClasses();
          vm.getBackgrounds();
          vm.getAlignments();
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('dc')
        .factory('basicInfoService', basicInfoService);

    // factory.$inject = ['dependencies'];

    /* @ngInject */
    function basicInfoService(dbService) {

        var service = {
            getRaces: getRaces,
            getClasses: getClasses,
            getBackgrounds: getBackgrounds,
            getSubraces: getSubraces,
            getAlignments: getAlignments,
            getSpeed: getSpeed,
            getHitDice: getHitDice,
            getArchetypes: getArchetypes
        };

        return service;

        function getRaces() {
          var db = dbService.newDB();
          return dbService.getKeys(db, 'races', 'name');
        }

        function getClasses() {
          var db = dbService.newDB();
          return dbService.getKeys(db, 'classes', 'name');
        }

        function getBackgrounds() {
          var db = dbService.newDB();
          return dbService.getKeys(db, 'backgrounds', 'name');
        }

        function getSubraces(raceName) {
          var db = dbService.newDB();
          return dbService.getItems(db, 'races', 'name', raceName).then(function(race){
            return race.subraces;
          });
        }

        function getAlignments() {
          var db = dbService.newDB();
          return dbService.getKeys(db, 'alignments', 'name');
        }

        function getSpeed(race) {
          var db = dbService.newDB();
          return dbService.getItems(db, 'races', 'name', race).then(function(race){
            return race.speed;
          });
        }

        function getHitDice(className) {
          var db = dbService.newDB();
          return dbService.getItems(db, 'classes', 'name', className).then(function(classInfo){
            return classInfo.hitDice;
          });
        }

        function getArchetypes(className) {
          var db = dbService.newDB();
          return dbService.getItems(db, 'classes', 'name', className).then(function(classInfo){
            return classInfo.specializations;
          });
        }
    }
})();

(function() {
  'use strict';

  angular
    .module('dc')
    .controller('ClassFeaturesController', ClassFeaturesController);

  // Controller.$inject = ['dependencies'];

  /* @ngInject */
  function ClassFeaturesController($scope, $rootScope, classFeaturesService, $log, $document, $mdDialog) {
    var vm = this;

    $rootScope.$on('classChanged', function(event, className) {
      $log.log(className);
      vm.getClassFeatures(className);
    });

    vm.getClassFeatures = function(className) {
      classFeaturesService.getClassFeatures(className).then(function(classFeatures) {
        // $log.log(classFeatures);
        vm.classFeatures = classFeatures;
        $log.log(vm.classFeatures);
        $scope.$digest();
      });
    }

    activate();

    function activate() {
      // vm.getClassFeatures('Barbarian');
    }

    vm.showClassFeaturesDialog = function(ev, className, archetypeName, level) {
      $mdDialog.show({
        controller: CombatStatsDialogController,
        templateUrl: '/app/character-sheet/class-features/class-features.dialog.html',
        parent: angular.element($document[0].body),
        targetEvent: ev,
        clickOutsideToClose: true,
        controllerAs: 'dialog',
        locals: {
          //   id: id,
          //   title: title,
          className: className,
          archetypeName: archetypeName,
          level: level
            //   abilityModifier: abilityModifier,
            //   combatStats: combatStats
        }
      });
    };

    function CombatStatsDialogController($mdDialog, className, archetypeName, level, characterService, classFeaturesService, basicInfoService, $log) {
      var vm = this;
      vm.className = className;
      vm.archetypeName = archetypeName;
      vm.level = level;
      //Cancel the Dialog
      // $log.log(vm.className);
      vm.cancel = function() {
        $mdDialog.cancel();
      };

      //Save the combat stats
      vm.save = function() {
        // characterService.updateCharacter(vm.id, {
        //   combatStats: combatStats
        // });
      };

      vm.getClassFeatures = function(className) {
        classFeaturesService.getClassFeatures(className).then(function(classFeatures) {
          angular.forEach(classFeatures.abilities, function (feature) {

            feature.level = parseFloat(feature.level);
          });
          $log.log(classFeatures);
          vm.classFeatures = classFeatures;
          $scope.$digest();
        });
      };

      vm.getArchetypeFeatures = function(archetypeName) {
        // $log.log(archetypeName);
        classFeaturesService.getArchetypeFeatures(archetypeName).then(function(archetypeFeatures) {
          angular.forEach(archetypeFeatures.abilities, function (feature) {
            feature.level = parseFloat(feature.level);
          });
          vm.archetypeFeatures = archetypeFeatures;
          // $log.log(archetypeFeatures);
          $scope.$digest();
        });
      };

      vm.getClassList = function() {
        basicInfoService.getClasses().then(function(classList) {
          vm.classList = classList;
          $scope.$digest();
        });
      };

      vm.getArchetypesList = function() {
        basicInfoService.getArchetypes(vm.className).then(function(archetypes) {
          vm.archetypeList = archetypes;
          $scope.$digest();
        });
      };

      activate();

      function activate() {
        vm.getClassFeatures(vm.className);
        vm.getArchetypeFeatures(vm.archetypeName);
        vm.getClassList();
        vm.getArchetypesList();
      }

    }
  }
})();

(function() {
    'use strict';

    angular
        .module('dc')
        .factory('classFeaturesService', classFeaturesService);

    // factory.$inject = ['dependencies'];

    /* @ngInject */
    function classFeaturesService(dbService, $log) {
        var service = {
            getClassFeatures: getClassFeatures,
            getArchetypeFeatures: getArchetypeFeatures
        };

        return service;

        function getClassFeatures(className) {
          var db = dbService.newDB();
          return dbService.getItems(db, 'classFeatures', 'class', className).then(function(classFeatures){
            // $log.log(classFeatures);
            return classFeatures;
          });
        }

        function getArchetypeFeatures(archetypeName) {
          var db = dbService.newDB();
          return dbService.getItems(db, 'classFeatures', 'archetype', archetypeName).then(function(archetypeFeatures){
            return archetypeFeatures;
          });
        }
    }
})();

(function() {
  'use strict';

  angular
    .module('dc')
    .controller('CombatStatsController', CombatStatsController);

  // Controller.$inject = ['dependencies'];

  /* @ngInject */
  function CombatStatsController(statsService, $log, $mdDialog, $document) {
    var vm = this;
    vm.statMods = statsService.statMods;
    vm.descriptions = {
      armorClass: 'Your Armor Class (AC) represents how well your character avoids being wounded in battle. Things that contribute to your AC include the armor you wear, the shield you carry, and your Dexterity modifier.',
      initiative: 'At the beginning of every combat, you roll initiative by making a Dexterity check. Initiative determines the order of creaturesâ€™ turns in combat.',
      speed: 'Every character and monster has a speed, which is the distance in feet that the character or monster can walk in 1 round.'
    };
    
    activate();

    function activate() {

    }

    vm.showCombatStatsDialog = function(ev, id, title, name, description, abilityModifier, combatStats) {
      $mdDialog.show({
        controller: CombatStatsDialogController,
        templateUrl: '/app/character-sheet/combat-stats/combat-stats.dialog.html',
        parent: angular.element($document[0].body),
        targetEvent: ev,
        clickOutsideToClose: true,
        controllerAs: 'dialog',
        locals: {
          id: id,
          title: title,
          name: name,
          description: description,
          abilityModifier: abilityModifier,
          combatStats: combatStats
        }
      });
    };

    function CombatStatsDialogController($mdDialog, id, title, name, description, abilityModifier, combatStats, characterService) {
      var vm = this;

      vm.id = id;
      vm.description = description;
      vm.abilityModifier = abilityModifier;
      vm.name = name;
      vm.title = title;
      vm.combatStats = combatStats;
      
      //Cancel the Dialog
      vm.cancel = function() {
        $mdDialog.cancel();
      };

      //Save the combat stats
      vm.save = function(combatStats){
        characterService.updateCharacter(vm.id, {
            combatStats: combatStats
          });
      };
    }
  }
})();
